:root{
/* CSS HEX */
--punch: #d92c2a;
--bastille: #1c171e;
--pampas: #faf9f7;
--venus: #f0efef;
--careys-pink: #d49fa0;
--pale-slate: #c5c4c5;
--turkish-rose: #b47c72;
--corduroy: #6c7471;
--rose-fog: #e4bcb3;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.call{
  position: fixed;
  right: -3rem;
  top: 28rem;
  transform: rotate(270deg);
  background-color: var(--bastille) !important;
  font-weight: bolder;
  padding: 0.2rem 1rem 0 1rem;
  border-radius: 0.5rem 0.5rem 0 0;
}
.callIcon{
  color: white;
  text-decoration: none;
  transition: 0.5s;
  font-size: 1.3rem;
}
.callIcon:hover{
  color: var(--punch);
}

#scrollWrap{
  z-index: 2;
  background-color: var(--bastille);
  position: fixed;
  top: 0;
  height: 1rem;
  width: 100%;
}
#scrollBar{
  background-color: var(--punch);
  height: 1rem;
}

/* ===[ Nabvbar ]=== */

#navbar{
  margin-top: 0.3rem;
}
.nav-menu {
  /* background: linear-gradient(rgba(0, 0, 0, 0.637), rgba(0, 0, 0, 0.671)); */
  background-color: var(--bastille);
  padding: 16px;
  transition: all 0.7s;

  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 0rem 0 1.5rem 1.5rem;
}


.iconStyle {
  color: var(--punch);
}

.navtop{
  height: 1.5rem;
  background-color: #eee;
  color: var(--bastille);
  font-weight: 500;
  display: flex;
  justify-content: space-around;
}
.menu-item {
  font-size: 14px;
  letter-spacing: 1px;
  color: #eee;
  transition: color 0.5s;
  text-decoration: none;
  transform: 0.5s;
}

.menu-item:hover {
  color: var(--punch) !important;
}

.line1,
.line2,
.line3 {
  width: 23px;
  height: 3px;
  margin: 5px;
  transition: all 0.4s;
}

.change .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.change .line2 {
  opacity: 0;
}

.change .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

.custom-navbar {
  padding: 5px 30px;
}


/* ===[ Banner ]=== */

#banner{
  min-height: 100vh;
  background-color: var(--pampas);
  background: 
   linear-gradient(to left, rgba(255, 255, 255, 0.800) 50%, rgba(255, 255, 255, 0.800) 100%),
   url('https://abcscprod.azureedge.net/abc-of-money/-/media/Project/ABCL/Internal_Images_526x230/526x230/PM/What_is_the_procedure_to_withdraw_PF_amount_526_230_M_PM_076.svg') no-repeat center;
  background-size: cover; /* Resize the background image to cover the entire container */
}

.banner-wrapper{
  height: 100vh;
}

#banner img{
  height: 100vh;
  background-position: center; /* Center the image */
  object-fit: cover;
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

#banner h6{
  font-weight: bolder;
}
.banner-left-col{
  min-height: auto;
  color: var(--bastille);
  color: #2d3436;

  
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;

}
.banner-left-col h1{
  font-size: 4rem;
  font-weight: 800;
}
.whatsapp{
  color: white;
  transition: 0.5s;
  font-size: 1.5rem;
  text-decoration: none;
  background-color: var(--punch);
  width: fit-content;
  padding: 0.5rem 1rem 0.8rem 1rem;
  font-weight: bold;
  border-radius: 0.5rem;
}

.whatsapp:hover{
  opacity: 85%;

}

/* ===[ features ]=== */

#features{
  min-height: 100vh;
  background-color: var(--venus);
}

.title-cnt{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.underline{
  display: block;
  border-bottom: 0.3rem solid var(--punch);
  width: 6rem;
}
.features-row{
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
}

.featured-card{
  height: 400px;
  width: 350px;
  border: 2px solid white;
  background-color: white;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0.5rem;

  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition: 0.5s;
  cursor: pointer;
}

.featured-card:hover{
  border: 2px solid var(--punch);
}

.belt{
  display: flex;
  justify-content: space-around;
  background-color: var(--bastille);
  background-color: #ffffff;
  color: var(--bastille);
}
.belt img{
  width: 12rem;
  height: 8.5rem;

}









/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .logoText{
    font-size: 1.5rem;
  }
  .nav-menu {
    border-radius: 0rem 0 1rem 1rem;
  }
  .banner-left-col h1{
    font-size: 3rem;
  }

  .featured-card{
    height: 400px;
    width: 320px;
  }
  
}
